import React from 'react';
import {pensions} from './../data/policefire_pensions.json';
import toTitleCase from './utils.js';

const Results = (props) => {
  let stateResults = pensions.filter(d => d.name === props.selected);
  if (stateResults.length > 0) {
    stateResults = stateResults[0]['data'];

    const mostRecentYear = Math.max(...Object.keys(stateResults));

    const shareline = (
      `The ${toTitleCase(props.selected)} was funded at ${parseFloat(stateResults[mostRecentYear]['Rate of Funding'])} percent as of ${mostRecentYear}.`
    );

    const url = 'https://bettergov.org/news/public-safety-pensions';

    const shareURLFB = (
      `https://www.facebook.com/sharer/sharer.php?u=${url}&quote=${shareline} Check out other Illinois towns' public safety pensions using @BetterGov's tool.`
    );

    const shareURLTwitter = (
      `https://twitter.com/intent/tweet?url=${url}&text=${shareline} Check out more Illinois public safety pensions using this tool by @BetterGov:`
    );

    const shareURLEmail = (
      `mailto:?subject=Public safety pension fund health — Better Government Association&body=${shareline} Read the full report: ${url}`
    );

    const shareFacebook = (
      <a className='share facebook' href={shareURLFB} target='_blank'>
        <i className='fab fa-facebook' />
      </a>
    );

    const shareTwitter = (
      <a className='share twitter' href={shareURLTwitter} target='_blank'>
        <i className='fab fa-twitter' />
      </a>
    );

    const shareEmail = (
      <a className='share email' href={shareURLEmail}>
        <i className='fas fa-envelope' />
      </a>
    );

    return (
      <div className='results-component'>
        <h5>{shareline}</h5>
        <h6>Share this page</h6>
        <ul className='share-buttons'>
          <li>{shareFacebook}</li>
          <li>{shareTwitter}</li>
          <li>{shareEmail}</li>
        </ul>
      </div>
    );
  }

  return null;
};

export default Results;
