import React from 'react';
import ReactDOM from 'react-dom';
import * as d3 from 'd3';
import * as d3Chromatic from 'd3-scale-chromatic';
import Chart from './bars.js';
import _ from 'underscore';

class Bars extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      chart: new Chart()
    };
    this._handleWindowResize = _.debounce(this._handleWindowResize.bind(this), 100);
  }

  _handleWindowResize () {
    this.setState({
      containerWidth: ReactDOM.findDOMNode(this).parentNode.offsetWidth
    });
  }

  componentDidMount () {
    const items = [];

    this.props.pensions.forEach((item, i) => {
      let latestYear = Math.max(...Object.keys(item.data));
      items.push({
        name: item.name,
        latestYear: latestYear,
        fundingRate: parseFloat(item.data[latestYear]['Rate of Funding']),
        selected: item.name === this.props.selected
      });
    });

    this.state.chart.create('#bars', items, {
      xExtent: [-10, 150],
      xScale: d3.scaleLinear(),
      xAccessor: d => d.fundingRate,
      xTickFormat: (d, i) => i === 0 ? d + '%' : d,
      colorScale: d3.scaleSequential(d3Chromatic.interpolateRdYlGn),
      colorExtent: [40, 100],
      parent: this,
      labelAccessor: d => d.name
    });

    window.addEventListener('resize', this._handleWindowResize);
  }

  componentDidUpdate () {
    const items = [];

    this.props.pensions.forEach((item, i) => {
      let latestYear = Math.max(...Object.keys(item.data));
      items.push({
        name: item.name,
        latestYear: latestYear,
        fundingRate: parseFloat(item.data[latestYear]['Rate of Funding']),
        selected: item.name === this.props.selected
      });
    });

    this.state.chart.update(items);
  }

  render () {
    return (
      <div className='bars-component'>
        <div className='label'>
          <div className='left'><i className='fas fa-chevron-left' />Worse funding ratio</div>
          <div className='right'>Better funding ratio<i className='fas fa-chevron-right' /></div>
        </div>
        <div id='bars' />
      </div>
    );
  }
}

export default Bars;
