import React from 'react';
import ReactDOM from 'react-dom';
import d3 from './d3';
import Chart from './chart.js';
import _ from 'underscore';
// import singleLine from '../data/single-line.json';

class Lines extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      chart: new Chart()
    };
    this._handleWindowResize = _.debounce(this._handleWindowResize.bind(this), 100);
  }

  _handleWindowResize () {
    this.setState({
      containerWidth: ReactDOM.findDOMNode(this).parentNode.offsetWidth
    });
  }

  componentDidMount () {
    const parseYear = d3.timeParse('%Y');

    let points = {};
    if (this.props.selected) {
      points = this.props.pensions.filter(
        pensionData => { return pensionData.name === this.props.selected; }
      )[0].data;
    }

    const lineA = Object.keys(points).map(key => {
      return {
        'year': parseInt(key),
        'value': parseFloat(points[key]['Total Actuarial Liabilities'].replace(/,/g, ''))
      };
    });

    const lineB = Object.keys(points).map(key => {
      return {
        'year': parseInt(key),
        'value': parseFloat(points[key]['Actuarial Value of Assets'].replace(/,/g, ''))
      };
    });

    const lines = [lineA, lineB];
    // let yMax = d3.max(lines, line => d3.max(line, d => d.value));

    this.state.chart.create('#multiple-line-chart', lines, {
      // xTickFormat: d => `Q1 ${d3.timeFormat('%y')(d)}`,
      margins: {
        top: 15,
        right: 70,
        left: 40,
        bottom: 25,
      },
      yTickFormat: (d, i, o) => {
        d = d / 1000000;
        if (i === o.length - 1) {
          return d3.format('$')(d) + ' million';
        }
        return d;
      },
      // yExtent: [0, yMax],
      xAccessor: d => parseYear(d.year),
      labelAccessor: () => 'if this does not exist in your data, it can be anything!',
      xTickFormat: (d, i) => {
        if (i === 0) {
          return d.getFullYear();
        } else {
          return '\'' + String(d.getFullYear()).substr(2);
        }
      }
    });

    window.addEventListener('resize', this._handleWindowResize);
  }

  componentDidUpdate () {
    let points = {};
    if (this.props.selected) {
      points = this.props.pensions.filter(
        pensionData => { return pensionData.name === this.props.selected; }
      )[0].data;
    }

    const lineA = Object.keys(points).map(key => {
      return {
        'year': parseInt(key),
        'value': parseFloat(points[key]['Total Actuarial Liabilities'].replace(/,/g, ''))
      };
    });

    const lineB = Object.keys(points).map(key => {
      return {
        'year': parseInt(key),
        'value': parseFloat(points[key]['Actuarial Value of Assets'].replace(/,/g, ''))
      };
    });

    const lines = [lineA, lineB];

    this.state.chart.update(lines);
  }

  render () {
    if (this.props.selected) {
      return (
        <div className='line-component'>
          <div className='key'>
            <span className='magenta' />Liabilities
            <span className='blue' />Assets
          </div>
          <div id='multiple-line-chart' />
        </div>
      );
    } else {
      return (
        <div className='line-component empty' />
      );
    }
  }
}

export default Lines;
