import React from 'react';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import {pensions} from './../data/policefire_pensions.json';
import * as d3 from 'd3';
import * as d3Chromatic from 'd3-scale-chromatic';

const Table = (props) => {
  let data = pensions.filter(d => d.name === props.selected);
  let columns = [];
  const colorScale = d3.scaleSequential(d3Chromatic.interpolateRdYlGn)
    .domain([40, 100]);

  if (data.length > 0) {
    data = Object.keys(data[0].data).map(d => {
      let obj = data[0].data[d];
      obj['Year'] = parseFloat(d);
      return obj;
    }).reverse();

    columns = [
      {
        Header: 'Year',
        accessor: 'Year',
        maxWidth: 70
      },
      {
        Header: 'Funding ratio',
        accessor: 'Rate of Funding',
        Cell: d => <span className='key'><span style={{backgroundColor: colorScale(parseFloat(d.value))}} />{d.value}</span>
      },
      {
        Header: 'Assets',
        accessor: 'Actuarial Value of Assets',
        Cell: d => `$${d.value}`,
        className: 'amt',
        headerClassName: 'amt'
      },
      {
        Header: 'Liabilities',
        accessor: 'Total Actuarial Liabilities',
        Cell: d => `$${d.value}`,
        className: 'amt',
        headerClassName: 'amt'
      },
      {
        Header: 'Active members',
        accessor: 'Active Members',
        className: 'amt',
        headerClassName: 'amt'
      },
      {
        Header: 'Retired members',
        accessor: 'Retired Members',
        className: 'amt',
        headerClassName: 'amt'
      },
    ];
  }

  return (
    <ReactTable
      data={data}
      columns={columns}
      minRows={0}
      showPagination={false}
      noDataText={false}
      defaultSorted={[
        {
          id: 'Year',
          desc: true
        }
      ]}
    />
  );
};

export default Table;
