// PART 8: Hello Stores
import React from 'react';
import { render } from 'react-dom';
import Select from './components/select';
import Results from './components/results';
// import Grid from './components/grid';
import Bars from './components/bars';
import Lines from './components/lines';
import Table from './components/table';
import { pensions } from './data/policefire_pensions.json';
import ReactGA from 'react-ga';
// import pym from 'pym.js';

ReactGA.initialize('UA-18960172-5'); // Unique Google Analytics tracking number
ReactGA.pageview(window.location.href);

// const pymChild = new pym.Child();

class App extends React.Component {
  constructor () {
    super();
    this.state = {
      // selected: 'ADDISON FIREFIGHTERS PENSION FUND',
      selected: null
    };
  }

  componentDidUpdate () {
    ReactGA.event({
      category: 'Update App',
      action: 'Updated context',
      label: this.state.selected
    });

    // pymChild.sendHeight();
  }

  render () {
    return (
      <div className='container'>
        <div className='row'>
          <div className='col'>
            <Bars
              selected={this.state.selected}
              pensions={pensions}
              update={(d) => this.setState({selected: d})}
            />
          </div>
        </div>
        <div className='row'>
          <div className='col col--main'>
            <Select
              pensions={pensions}
              selected={this.state.selected}
              update={(d) => this.setState({selected: d})}
            />
            <Lines
              pensions={pensions}
              selected={this.state.selected}
            />
            <Results
              pensions={pensions}
              selected={this.state.selected}
            />
          </div>
        </div>
        <div className='row'>
          <div className='col'>
            <Table
              selected={this.state.selected}
            />
          </div>
        </div>
        <div className='row'>
          <div className='col'>
            <div className='graphic-footer'>
              <p>
                Source: <a href='http://cgfa.ilga.gov/Resource.aspx?id=5' target='_blank'>Illinois Commission on Government Forecasting and Accountability</a>, Downstate Police and Fire reports
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

render(<App />, document.getElementById('pension-app'));
